.container {
  min-width: 320px;
  margin: 0 auto;
  padding: 0 15px;
}

.title {
  margin: 20px auto;
}

.button {
  display: block;
  justify-content: center;
  text-align: center;
  color: #fff;
  border: 0;
  text-decoration: none;
  margin: 20px;
  padding: 8px 16px;
  font-family: inherit;
  font-size: 18px;
  line-height: 24px;
  font-style: normal;
  font-weight: 500;
  width: 180px;
  border-radius: 2px;
  background-color: #20b369;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.button:hover,
.button:focus {
  background-color: #ce331f;
}

.list {
  display: flex;
  flex-direction: row;
  list-style: none;
  justify-content: space-around;
}

.item {
  color: lightsalmon;
}

.title {
  text-align: center;
  color: #20b369;
}

.text {
  color: lightslategray;
  font-size: 14px;
}
